.menu-show{
  margin-top: 1rem;
  background-color: #fff;
  box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.12);
  border-radius: .4rem;
  display: flex;
  justify-content: space-between;
  span{
    line-height: 1.2rem;
    padding: 2rem 0 1.7rem 1.5rem;
    font-family: "Gilroy Medium", sans-serif;
  }
}

.menu-list{
  margin-top: .5rem;
  background-color: #fff;
  border-radius: .4rem;
  box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.12);
  .menu-list-item{
    border-left: 4px solid transparent;
    padding: 2rem 2rem 2rem 1.8rem;
    cursor: pointer;
    span{
      font-size: 1.6rem;
      color: #808080;
      font-family: "Gilroy Medium", sans-serif;

    }
  }
  .selected-list-menu-item{
    padding: 2rem 2rem 2rem 1.8rem;
    border-left: 4px solid;
    border-image: linear-gradient(to top, #fd2ac5, #3b096e) 4;
    color: #000;
    border-left: 4px solid transparent;
    cursor: pointer;
    span{
      font-size: 1.6rem;
      color: #000;
      font-family: "Gilroy Medium", sans-serif;

    }
  }
}

.gigs-menu{
  margin-top: 1.5rem;
  .gigs-menu-item{
    color: #808080;
    cursor: pointer;
    display: flex;
    justify-content: space-between;
    padding: .7rem 1.5rem .8rem 1.4rem;
    h5{
      font-size: 1.5rem;
      margin: 0;
      line-height: 1.6rem;
    }
    span{
      font-size: 1.6rem;
      line-height: 1.6rem;
    }
  }
  .gigs-menu-item-active{
    background-color: #61116a;
    color: #fff;
    cursor: pointer;
    display: flex;
    justify-content: space-between;
    padding: .5rem 1.5rem .6rem 1.4rem;
    border-radius: .4rem;
    h5{
      font-size: 1.5rem;
      margin: 0;
      line-height: 1.6rem;
    }
    span{
      font-size: 1.6rem;
      line-height: 1.6rem;
    }
  }
}

.gigs-paper{
  background-color: #f7f9fa;
  box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.12);
  border-radius: .4rem;
  margin-top: 1rem;
  min-height: calc(100vh - 9.4rem);
  .no-gigs{
    margin-top: 7.5rem;
    img{
      margin: 0 auto 0 auto;
      display: block;
    }
    h1{
      text-align: center;
      font-family: "Gilroy Medium", sans-serif;
    }
    h5{
      color: #808080;
      font-size: 1.6rem;
      width: 100%;
      text-align: center;
      font-family: "Gilroy Medium", sans-serif;
    }
  }
  .gigs-menu{
    display: flex;
    justify-content: flex-start;
    background-color: #fff;
    border-bottom: .1rem solid rgba(128,128,128,0.2);
    border-radius: 0.4rem 0.4rem 0 0;
    span.active{
      border-bottom: .2rem solid #eb3386;
      color: #000
    }
    span{
      color: #808080;
      font-size: 1.6rem;
      line-height: 1.6rem;
      padding: 1.9rem .6rem 1.3rem .6rem;
      margin: 0 1.1rem 0 1.1rem;
      cursor: pointer;
      font-family: "Gilroy Medium", sans-serif;
    }
  }
}

.gig-card-container {
  background-color: #fff;
  border-radius: 0.4rem;
  box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.12);
  width: 23.9rem;
  background-image: url("/static/img/card_background.png");
  background-repeat: no-repeat;
  margin: 0 calc((100% - 717px)/3) 2.5rem 0;
  .gig-card-completed-buttons{
    border-top: .1rem solid rgb(217, 217, 217);
    height: 5.1rem;
    display: flex;
    border-radius: .4rem;
    font-size: 1.6rem;
    line-height: 1.3rem;
    .completed-button{
      color: #61116a;
      flex-grow: 1;
      font-family: "Gilroy Medium", sans-serif;
      cursor: pointer;
      display: flex;
      align-items: center;
      justify-content: center;
      span{
        text-align: center;
      }
    }
    .completed-button:hover{
      color: #fff;
      background-color: #61116a;
    }
    .completed-button:hover:last-child{
      border-radius: 0 0 .4rem 0;
    }
    .completed-button:hover:first-child{
      border-radius: 0 0 0 .4rem;
    }
    .completed-button:hover:last-child:first-child{
      border-radius: 0 0 .4rem .4rem;
    }
    .no-buttons{
      text-align: center;
      font-family: "Gilroy Medium", sans-serif;
      align-self: center;
      flex-grow: 1;
      color:#808080;
    }
  }
  h5 {
    margin: 1rem 0 1rem 0;
  }
  .gig-card-header{
    display: flex;
    justify-content: space-between;
    padding-left: 1.4rem;
    margin-bottom: .9rem;
    h5{
      margin: 1.4rem 0 0 0 ;
      span{
        font-family: "Gilroy ExtraBold", sans-serif;
        font-size: 1.6rem;
      }
    }
    .gig-card-pills-container {
      display: flex;
      justify-content: flex-end;
      height: 2.2rem;
      margin: 1.1rem 0.7rem .5rem 1.1rem;
      overflow-y: auto;
      span {
        border-radius: 1.1rem;
        font-size: 1.2rem;
        background-color: #61116a;
        color: #fff;
        padding: 0.3rem 1.1rem 0 1.1rem;
        margin-right: 0.4rem;
      }
    }
  }

    .gig-card-name{
      display: flex;
      justify-content: space-between;
      margin: 0 1.1rem 0 1.1rem;
      h4{
        color: #000;
        font-size: 1.8rem;
      }

      span{
        padding: .9rem 0 0 0;
        text-align: center;
        font-size: 1.2rem;
        font-family: "Gilroy Medium";
      }

    h4:before {
      position: relative;
      content: "";
      width: 0.9rem;
      height: 0.9rem;
      display: inline-block;
      border-radius: 50%;
      margin: 0 0.6rem 0 0.3rem;
      bottom: 0.1rem;
    }
    h4.blue:before {
      background-color: #808bfe;
    }
    h4.sky-blue:before {
      background-color: #72d6ff;
    }
  }
  .gig-card-date, .gig-card-location, .gig-card-budget {
    color: #808080;
    font-size: 1.4rem;
    display: flex;
  }
  .gig-card-date {
    text-transform: uppercase;
    margin: 0 1.1rem 0 1.1rem;
  }
  .gig-card-location, .gig-card-budget {
    margin: 0 1.1rem 0 0.9rem;
  }
  .gig-card-location:last-child{
    margin-bottom: 2.3rem;
  }
}

.gigs-container{
  margin: 2.5rem 2.3rem 0 2.3rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}

.navigation{
  padding-top: 2rem;
  padding-bottom: 8.3rem;
  display: flex;
  justify-content: center;
  span{
    font-size: 1.4rem;
    line-height: 1.1rem;
    padding: 1.1rem 1.5rem 1rem 1.4rem;
    border: .1rem solid #e4e4e4;
    border-right: none;
    cursor: pointer;
    background-color: #fff;
  }
  span:first-child{
    border-radius: .4rem 0 0 .4rem;
    width: 8.6rem;
    text-align: center;
  }
  span:last-child{
    border-radius: 0 .4rem .4rem 0;
    width: 8.6rem;
    text-align: center;
    border-right: .1rem solid #e4e4e4;
  }
  span.disabled{
    color: rgba(128,128,128, 0.5);
  }
  span.active{
    background-color: #61116a;
    color: #fff;
  }
}

.payment-dialog{
  max-width: 36.2rem;
  .gig-card-name{
    display: flex;
    justify-content: space-between;
    padding: 0 2.4rem 2rem 2.4rem;
    border-bottom: .1rem solid rgba(128,128,128,0.2);
    h4{
      color: #000;
      font-size: 1.8rem;
      margin: 0;
    }

    span {
      border-radius: 1.1rem;
      font-size: 1.2rem;
      background-color: #61116a;
      color: #fff;
      padding: .5rem 1.1rem .4rem 1.1rem;
      margin-left: 1.5rem;
      font-family: "Gilroy Medium", sans-serif;
    }

    h4:before {
      position: relative;
      content: "";
      width: 0.9rem;
      height: 0.9rem;
      display: inline-block;
      border-radius: 50%;
      margin: 0 0.8rem 0 0.3rem;
      bottom: 0.1rem;
    }
    h4.blue:before {
      background-color: #808bfe;
    }
    h4.sky-blue:before {
      background-color: #72d6ff;
    }
  }

  .dialog-gig-info{
    display: flex;
    justify-content: flex-start;
    padding: 2rem 2.4rem 0 2.4rem;
    .gig-card-date, .gig-card-location {
      color: #000;
      font-size: 1.4rem;
      display: flex;
    }
    .gig-card-date {
      text-transform: uppercase;
      margin: 0 2.1rem 0 0;
    }
  }
  .amount{
    display: flex;
    justify-content: flex-start;
    padding: 3rem 2.4rem 2.1rem 2.4rem;
    border-bottom: .1rem solid rgba(128,128,128, 0.2);
    line-height: 1.3rem;
    h5{
      color: #808080;
      font-size: 1.6rem;
      padding-right: 2.4rem;
      margin: 0;
      line-height: 1.3rem;
    }
    p{
      color: #000;
      margin: 0;
      span{
        font-family: "Gilroy ExtraBold", sans-serif;
        font-size: 1.6rem;
      }
    }
  }
  .amount:last-child{
    border-bottom: none;
    h5{
      padding-right: 4.7rem;
    }
  }
  .payment-button-row{
    padding: 2rem 2.4rem 0 2.4rem;
    display: flex;
    justify-content: flex-end;
    span{
      color: #fff;
      font-family: "Gilroy ExtraBold", sans-serif;
      font-size: 1.6rem;
      line-height: 1.6rem;
      padding: 1rem 3.5rem 1rem 3.4rem;
      background-color: #61116a;
      border-radius: 1.2rem;
    }
  }
  .input-hours{
    background-color: #f4f4f4;
    padding: 0 2.4rem 2rem 2.4rem;

  }
}